* {
  margin: 0;
  padding: 0;
  /* max-width: 2000px; */
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
html,
body {
  background-color: #080a21;

  color: white;

  font-size: 1rem;
  font-family: "Source Sans Pro", sans-serif;
  box-sizing: border-box;
  // scroll-behavior: smooth; /*This will make it so when you click on a link,
  // it will scroll to that spot rather than jump*/

  margin-left: auto;
  margin-right: auto;
}
// MAIN SECTION -- contains all of the routers

.main-section {
  background-color: #080a21;
  background-image: url("./images/bg14.jpg");
  background-position: top -160px left;
  background-size: 1200px 2000px;
  background-repeat: no-repeat;
  // padding: 1rem;
  // padding-top: 1rem;
  position: relative;
  height: 1900px;
  min-height: 940px;
  // max-height: 3000px;

  display: flex;
  flex-direction: column;
}
