//   color: rgba(255, 255, 255, 0.7) !important; // The links on the side menu that aren't clickable
// }

@media (min-width: 500px) {
  .main-section {
    flex-direction: row;
    padding: 2rem;
  }
}
@media (min-width: 800px) {
  .main-section {
    background-position: top -200px left;
    background-size: 1600px 1600px;
    height: 1600px;

    // min-height: 800px;
  }
}

@media (min-width: 1400px) {
  .main-section {
    background-size: 3200px 3200px;
    background-position: top -255px left -300px;
  }
}

// The breadcrumb menu shows up when the width is less than 500px
