// Ability Page

.ability-table {
  margin-top: 1.5rem;
  margin-bottom: 1rem;
}

.diceSection {
  display: flex;
  flex-direction: column;
  width: 70%;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
}
.diceImageDisplay {
  flex-direction: row;
}
.diceImage {
  width: 2rem;
  margin: 2rem;
  margin-top: 1rem;

  animation: shake 0.5s;
  animation-iteration-count: 1;
  transform: translate3d(0, 0, 0);
  perspective: 1000px;

  @keyframes shake {
    0% {
      transform: translate(1px, 1px) rotate(0deg);
    }
    10% {
      transform: translate(-1px, -2px) rotate(-10deg);
    }
    20% {
      transform: translate(-3px, 0px) rotate(10deg);
    }
    30% {
      transform: translate(3px, 2px) rotate(0deg);
    }
    40% {
      transform: translate(1px, -1px) rotate(6deg);
    }
    50% {
      transform: translate(-1px, 2px) rotate(-6deg);
    }
    60% {
      transform: translate(-3px, 1px) rotate(0deg);
    }
    70% {
      transform: translate(3px, 1px) rotate(-1deg);
    }
    80% {
      transform: translate(-1px, -1px) rotate(1deg);
    }
    90% {
      transform: translate(1px, 2px) rotate(0deg);
    }
    100% {
      transform: translate(1px, -2px) rotate(-6deg);
    }
  }
}
.character-summary {
  text-align: left;
  border: rgb(214, 206, 195) solid 1px;
  border-radius: 4px;
  padding: 1.5rem;
  margin-bottom: 2rem;
  margin-top: 2rem;
